
.helpRank {
  background: #FFF;
}
.help-rank-head {
  width: 100%;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 5px;
}
.help-rank-head>ul {
  display: inline-block;
  width: 42%;
  height: 50px;
  margin: 0 5px;
  border: 1px solid #F69B81;
  border-radius: 100px;
  position: relative;
}
.help-rank-head li {
  display: inline-block;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #F69B81;
  letter-spacing: -0.43px;
}
.help-rank-head ul>li:first-child {
  text-align: center;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  background: #F69B81;
  position: absolute;
  top:5px;
  left: 10px;
}
.help-rank-head ul>li:last-child {
  text-align: left;
  position: absolute;
  top:8px;
  left: 55px;
  line-height: 1.5em;
}
.help-rank-head li>p {
  font-family: PingFangSC-Semibold;
  font-size: 18px;
  color: #BE6D45;
  letter-spacing: 0;
}
.help-rank-head ul img {
  width: 20px;
  height: 20px;
  margin-top: 10px;
  /*margin-bottom: 10px;*/
}
.help-rank-body ul {
  display: block;
  width: 100%;
  height: 145px;
}
.help-rank-body ul>li {
  display: block;
  width: 100%;
  height: 65px;
  line-height: 65px;
  border-top: 1px #EBEBEB solid;
  vertical-align: middle;
  position: relative;
}
.help-rank-order {
  display: inline-block;
  width: 20%;
  height: 35px;
  border-top-right-radius: 35px;
  border-bottom-right-radius: 35px;
  text-align: right;
  line-height: 35px;
  padding-right: 20px;
  font-family: PingFangSC-Semibold;
  font-size: 14px;
  letter-spacing: 0;
  color: #F69B81;
  position: absolute;
  top: 15px;
}
.help-rank-order-selected {
  background: #F69B81;
  color: #FFFFFF;
}
.help-rank-Avator {
  display: inline-block;
  width: 55px;
  height: 55px;
  margin-left: 30%;
  margin-right: 20px;
  margin-top: 5px;
  border-radius: 55px;
}
.help-rank-playerInfo {
  display: inline-block;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #000000;
  letter-spacing: -0.43px;
  line-height: 1.5em;
  position: relative;
  top: -15px;
}
.help-rank-playerInfo>span {
  font-family: PingFangSC-Semibold;
  font-size: 18px;
  color: #BE6D45;
  padding: 0 4px;
}



