
.score-detail-head {
  display: block;
  height: 120px;
  width: 100%;
  background: url('/static/img/scoreShop/mainImg.png');
  background-color: #F5DA55;
  background-size: 100%;
  text-align: center;
}
.score-detail-head>dl {
  display: inline-block;
  width: 49%;
  margin: 35px auto;
  color: #1A1C2C;
  font-family: PingFangSC-Medium;
}
.score-detail-head>dl dt {
  font-size: 16px;
  letter-spacing: -0.27px;
}
.score-detail-head>dl dd {
  font-size: 25px;
  letter-spacing: -0.42px;
}
.score-detail-icon {
  background: #83D9BA;
  border-radius: 100px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px;
  position: absolute;
  right: 10px;
  top: 20px;
  font-size: 10px;
  color: #FFFFFF;
  letter-spacing: -0.17px;
}
.score-detail-value {
  position:absolute;
  top:20px;
  right:120px;
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #1A1C2C;
  letter-spacing: -0.23px;
}
.score-desc {
  width: 100%;
  margin: 10px auto;
}
.score-desc>p {
  padding-left: 15px;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #1A1C2C;
  letter-spacing: -0.27px;
}
.score-desc>ul {
  display: block;
  margin-left: 30px;
}
.score-desc>ul li{
  width: 100%;
  list-style-type: decimal;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #7F7F7F;
  letter-spacing: -0.23px;
  line-height: 40px;
}
.vux-label-desc {
  /* 2017-11-05 11:03:10: */
font-family: PingFangSC-Medium!important;
font-size: 12px!important;
color: #999999!important;
letter-spacing: -0.2px!important;
}
.vux-label {
  font-family: PingFangSC-Medium!important;
  font-size: 14px!important;
  color: #1A1C2C!important;
  letter-spacing: -0.23px!important;
}



