/**/
.BigPhotos {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 11;
    background: rgba(0,0,0,0.9);
    overflow-y: visible;
    padding-top: 40%;
}
.BigPhotos div {
	height: 250px!important;
}
.BigPhotos img{
	z-index: 100;
	width: 100%;
	height: 100%;
    margin: 0 auto;
}
.goShopStyle {
	display: block;
	font-family: PingFangSC-Regular;
	font-size: 14px!important;
	color: #ED8C7F!important;
	letter-spacing: -0.4px;
  border: 1px solid #ED8C7F!important;
  border-radius: 4px!important;
  width: 120px!important;
  margin-top: 20px!important;
  margin: 0 auto;
}
.emptyOrder>p{
	font-family: PingFangSC-Regular;
	font-size: 16px;
	color: #333333;
	letter-spacing: -0.46px;
	width: 100%;
	margin:20% auto;
  margin-top: 40%;
	text-align: center;
	line-height: 40px;
}
.emptyOrder>p>span {
	font-size: 12px;
	color: #979797;
	letter-spacing: -0.34px;
}
.ximg-demo {
width: 100%;
height: auto;
margin-bottom: -8px;
}
.ximg-error {
background-color: yellow;
}
.ximg-error:after {
content: '';
color: red;
}
/*  */
.SetsChoiceness {
  background: #FFF;
}
.mall_title{
  display: block;
  padding-top: 14px;
  padding-bottom: 14px;
}
.mall_title>p{
  width: 90%;
  margin: 6px auto;
  text-align: left;
  font-family: PingFangSC-Medium;
  font-size: 15px;
  color: black;
  letter-spacing: 1.22px;
  border-left: 4px #ED8C7F solid;
  padding-left: 10px;
}
.mall_title>p span{
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #999;
  float: right;
}
.list_choiceness p{
  font-family: PingFangSC-Medium;
  padding-left: 10px;
  text-align: left;
}
.list_choiceness>li {
  display: inline-block;
  width: 50%;
  margin: 0 auto;
  margin-bottom: 8px;
}
.list_choiceness>li div {
  width: 96%;
  margin: 0 auto;
}
.list_choiceness>li img {
  width: 100%;
}
.choiceness_desc {
  font-size: 14px;
  color: #666666;
  letter-spacing: -0.4px;
  line-height: 20px;
}
.choiceness_price {
  font-size: 16px;
  color: #FF5A54;
  letter-spacing: -0.46px;
}
.choiceness_price del {
  font-size: 14px;
  color: #999999;
}
/* wshop */
.wshop-orderPage {
  width: 100%;
  /*height: 220px;*/
  font-family: PingFangSC-Medium;
  letter-spacing: 0.3px;
  background: #FFF;
}
.wshop-orderPage>p {
  font-size: 14px;
  color: #333333;
}
.wshop-orderPage>p>span {
  height: 15px;
  line-height: 15px;
  font-size: 10px;
  color: #FF5A54;
  border: 1px #FF5A54 solid;
  border-radius: 2px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 20px;
  margin-right: 10px;
}
.wshop-orderPage-input {
  margin: 10px auto;
}
.weui-cells:after,.weui-cell:before, .weui-cells:before  {
  border: transparent!important;
}
.weui-input {
  border: 1px #E7E7E7 solid!important;
  padding-left: 10px!important;
  margin-left: -10px;
}
/*item*/
.wshop-orderCard {
  position: relative;
  display: block;
  width: 100%;
  margin: 10px auto;
  margin-top:-8px;
  padding-top: 20px;
  padding-bottom: 10px;
  background: #fff;
}
.wshop-orderCard>dt {
  display: inline-block;
  width: 49%;
  height: 100px;
  text-align: center;
}
.wshop-orderCard>dt img {
  width: 90%;
  height: 100%;
}
.wshop-orderCard>dd {
  width: 49%;
  height: 95px;
  line-height: 2em;
  display: inline-block;
  vertical-align: top;
  font-family: PingFangSC-Regular;
  font-size: 13px;
  color: #333333;
  letter-spacing: -0.37px;
}
.vux-label {
  font-family: PingFangSC-Regular!important;
  font-size: 13px!important;
  color: #9B9B9B!important;
  letter-spacing:-0.37px!important;
}
.order-detail-Tips{
  width: 100%;
  background: #FFF;
  padding-top: 10px;
  padding-bottom: 10px;
}
.order-detail-Tips>p {
  padding-left: 5%;
  font-size: 14px;
}
.order-detail-Tips>ol {
  width: 80%;
  display: block;
  margin: 8px auto;
  font-size: 12px;
}
.vux-cell-form-preview {
  width: 85%;
  margin: 10px auto;
  border-bottom: 1px #E1E1E1 solid!important;
  padding-bottom: 10px;
}
.weui-form-preview__item {
  margin: 12px auto!important;
}
.weui-form-preview__label {
  font-family: PingFangSC-Medium;
  font-size: 13px;
  color: #666666;
  letter-spacing: -0.3px;
  line-height: 20px;
}
.weui-form-preview__value {
  font-family: PingFangSC-Medium;
  font-size: 13px;
  color: #333333;
  text-align: left;
  padding-left: 30px;
  letter-spacing: -0.3px;
  line-height: 20px;
}
.couponStyle span {
  width: 80px;
  height: 15px;
  position: relative;
  left: -15px;
  font-family: PingFangSC;
  font-size: 12px;
  color: #FFFFFF;
  letter-spacing: -0.27px;
  line-height: 15px;
  margin:0 8px;
  z-index: 5;
  text-align: center;
  white-space:nowrap;
  overflow-x: scroll;
  padding-left: 5px;
  padding-right: 5px;
}
.couponStyle span:first-child {
  margin-right:6px;
  z-index: 5;
}
.couponStyle>span img {
  width:80px;
  height:15px;
  position: absolute;
  top: 0;
  left:0;
  z-index: -1;
}
.getCouponPage-header {
  width: 100%;
  text-align: center;
  font-family: PingFangSC-Medium;
  font-size: 16px;
  color: #333333;
  padding-top: 5px;
}
.wshop-getCouponPage>ul p:last-child {
  border-bottom: none!important;
}
.get-coupon-btn {
  color:#FF5A54!important;
  border:1px #FF5A54 solid!important;
  position:absolute!important;
  right:10px!important;
  bottom: 20px!important;
}
.get-coupon-btn:disabled {
  background: #E1E1E1;
  border:1px #E1E1E1 solid!important;
  color: #000!important;
}
.palette-child-button {
  width: 90%!important;
  height: 90%!important;
  border-radius: 50%!important;
  font-size: 12px!important;
  background: lightgreen!important;
}
