.mainSwiper {
  position: absolute;
  top: 0;
  bottom: 60px;
  left: 0;
  right: 0;
}
.mainSwiper img {
  width: 100%;
  height: 100%;
}
.pullUp {
  position: absolute;
  bottom: 70px;
  font-size: 40px!important;
  color: #FFF;
  z-index: 1;
  left: 50%;
  margin-left: -15px;
}
.module_title{
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #F4F4F4;
}
.module_title>p{
  width: 90%;
  margin: 6px auto;
  text-align: left;
  font-family: PingFangSC-Medium;
  font-size: 15px;
  color: black;
  letter-spacing: 1.22px;
  border-left: 4px #ED8C7F solid;
  padding-left: 10px;
}
.module_title>p span{
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #999;
  float: right;
}
.module_content {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #FFF;
  overflow-x: hidden;
  overflow-y: hidden;
}
.weui-cell:before, .weui-cells:before,.weui-cells:after {
  border: none;
}
.activity-rule-title {
  width: 100%;
  text-align: center;
  font-family: PingFangSC-Medium;
  font-size: 15px;
  color: #F5405E;
  letter-spacing: -0.4px;
  text-decoration: underline;
}
.activity-rule-header {
  width: 100%;
  background: #EB465E;
  color: #FFF;
  padding-top: 5px;
  padding-bottom: 5px;
}
.activity-rule-tips {
  width: 90%;
  margin: 0 auto;
  text-align: left;
  font-weight: lighter;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #000000;
  letter-spacing: -0.32px;
  line-height: 1.5em;
}
.footer {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-top: 1px solid #D9D9D9;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #999;
}
.footer span {
  color: #666;
  padding-left: 5px;
  padding-right: 5px;
}
.marketing-notice-bar {
  position: fixed;
  top: 50px;
  z-index: 10;
  background: rgba(91,91,91,0.6);
  width: 100%;
  text-align: left;
  /*text-align: center;*/
  color: #FFF;
  font-size: 14px;
  height: 24px;
  line-height: 24px;
}
.notice-icon {
  margin-left: 10px;
  margin-right: 5px;
}
.notice-close {
  position: absolute;
  top: 1px;
  right: 5px;
}



