
.friend-rank-body {
  text-align: left;
  overflow-x: scroll;
  white-space: nowrap;
}
.friend-rank-body::-webkit-scrollbar {
    display: none;
}
/*.friend-rank-body>li:first-child {
  margin-left: 20px;
}*/
.friend-rank-body>li {
  text-align: center;
  display: inline-block;
  margin: 0 10px;
  line-height: 100%;
}
.friend-rank-body>li p {
  font-family: PingFangSC-Regular;
  font-size: 10px;
  color: #000000;
  letter-spacing: -0.36px;
}
.friend-rank-body img {
  width: 55px;
  height: 55px;
  border-radius: 55px;
  display: inline-block;
}



