
.member-task>img {
  width: 100%;
}
.icon-renwu {
}
.member-task-body i {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background: #FEDC3D;
  font-size: 20px;
  color: #FFF;
  padding: 8px;
  margin-right: 10px;
}
.member-task-title {
  font-family: PingFangSC-Regular;
  font-size: 15px;
  color: #333333;
  letter-spacing: -0.4px;
}
.member-task-complete {
  background: #FAC129;
  border-radius: 100px;
  font-family: PingFangSC-Semibold;
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: -0.37px;
  width: 85px;
  text-align: center;
  height: 35px;
  line-height: 35px;
}
.member-task-todo {
  background: #6CC1F8;
  border-radius: 100px;
  font-family: PingFangSC-Semibold;
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: -0.37px;
  width: 85px;
  width: 85px;
  text-align: center;
  height: 35px;
  line-height: 35px;
}



