
.send-title {
  display: inline-block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #1B0603;
  letter-spacing: -0.32px;
}
.confirmBagBody {
  /* : */
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #000000;
  letter-spacing: -0.25px;
  background: #EBCF47;
}
#send-title > span {
  color: #E54855;
}
.send-tip {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #1B0603;
  letter-spacing: -0.32px;
}
.redbag4-item {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #EB465E;
  border: 1px #EB465E solid;
  border-radius: 5px;
  padding-left: 3px;
  padding-right: 3px;
  margin: 5px;
}
.redbag4-item-selected {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  border-radius: 5px;
  padding-left: 3px;
  padding-right: 3px;
  margin: 5px;
  color: #FFF;
  background: #EB465E;
  border: 1px #EB465E solid;
}
.red-bag-btn {
  padding: 1px 20px;
  border-radius: 20px;
  margin-bottom: 10px;
  margin: 10px 10px;
  margin-top: -20px !important;
}
.pay-confirm-tip {
  /* : */
  display: inline-block;
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #000000;
  letter-spacing: -0.25px;
  padding-left: 20px;
  padding-right: 2%;
}
.pay-tip {
  /* : */
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #000000;
  letter-spacing: -0.25px;
  margin-top: -5px;
  margin-bottom: 10px;
}



