
.coupon-detail-head {
  width: 100%;
  background: #EE903C;
  text-align: center;
  padding-top: 25px;
  padding-bottom: 25px;
}
.coupon-detail-small {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  letter-spacing: -0.32px;
}
.coupon-detail-code {
  background: #FFF;
  width: 80%;
  margin: 0 auto;
  margin-top: 10px;
  position: relative;
}
.coupon-detail-module {
  margin: 8px auto;
  width: 100%;
}
.coupon-detail-title {
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #888888;
  letter-spacing: -0.32px;
  padding-left: 8px;
}
.coupon-detail-desc {
  background: #FFF;
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.37px;
  padding: 8px;
}
.coupon-detail-desc ul {
  padding-left: 15px;
}



