.developerIcon {
	display: block;
	width: 30%;
	margin: 20px auto;
  /*padding-bottom: 20px;*/
	margin-left: 50%;
  transform: translateX(-50%);
}
.ma-rule,.ma-contact {
  width: 90%;
  margin: 0 auto;
  background: #FFF;
  padding-bottom: 10px;
  border-radius: 5px;
  margin-top: 50px;
}
.ma-rule-title,.ma-contact-title {
  position: relative;
  text-align: center;
  font-family: PingFangSC-Medium;
  font-size: 18px;
  color: #333333;
  letter-spacing: 0;
  padding-top: 10px;
  padding-bottom: 10px;
}
.ma-rule-title>span {
  position: absolute;
  right: 10px;
}
.ma-rule-content,.ma-contact-content {
  border: 1px #E5E5E5 solid;
  padding: 10px;
  margin-bottom: 10px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #333333;
  letter-spacing: 0;
}
.ma-contact-content>ol {
  margin-left: 10%;
}
.ma-rule-btn,.ma-contact-btn {
  width: 90%!important;
  background: #F2AE43!important;
}
.ma-contact-content>ol {
  list-style: none;
}
.ma-footer {
  width: 100%;
  margin: 0 auto;
  margin-top: 25px;
  height: 40px;
}
.ma-footer li {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  letter-spacing: -0.32px;
}
.ma-footer li:first-child {
  background: #EBCE44;
  color: #000000;
}
.ma-footer li:nth-child(2) {
  background: #000;
  color: #FFFFFF;
}
.ma-footer li:last-child {
  background: #E0E0E0;
  color: #000000;
}
.ma-footer li img {
  width: 20px;
  position: relative;
  top: 5px;
}


