
.member-achieve {
  background: #F7F7F7;
  width: 100%;
  height: 100%;
}
.member-achieve-body i {
/*  width: 60px;
  height: 60px;*/
  border-radius: 30px;
  background: #FEDC3D;
  font-size: 30px;
  color: #FFF;
  padding: 8px;
  margin-right: 10px;
}
.iconBg1 {
  background: #F85766;
}
.iconBg2 {
  background: #FCB03C;
}
.iconBg3 {
  background: #A054D5;
}
.member-achieve-title {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #666666;
  letter-spacing: -0.37px;
}
.member-achieve-complete {
  background: #FAC129;
  border-radius: 100px;
  font-family: PingFangSC-Semibold;
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: -0.37px;
  width: 85px;
  text-align: center;
  height: 35px;
  line-height: 35px;
}
.member-achieve-todo {
  /* Rectangle 3: */
  border: 1px solid #DFDFDF;
  /* : */
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #DFDFDF;
  letter-spacing: -0.32px;
  width: 75px;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.member-achieve-progress {
  display: block;
  position: absolute;
  bottom: 10px;
  left: 70px;
  width: 60%;
  background: #FF5A54;
}
.member-achieve-progress-value {
  position: absolute;
  bottom: 2px;
  left: 82%;
}



