
#maMomShow {
  width: 100%;
  background: #FFF;
}
.mom-show-body {
  /*position: relative;*/
  margin-top: 50px;
  width: 100%;
}
#mom-slide-barrage {
  z-index: 50;
  width: 100%;
  position: fixed;
  top: 120px;
}
.ma-musicControl {
  z-index: 40;
  position: fixed;
  top: 100px;
  right: 33px;
  width: 33px;
}
.ma-barrageControl {
  z-index: 40;
  position: fixed;
  top: 100px;
  left: 20px;
  width: 33px;
  height: 33px;
}
.ma-barrageControl img {
  width: 100%;
  height: 100%;
}
.ma-hotline {
  z-index: 40;
  position: fixed;
  top: 140px!important;
  width: 35px;
  left: 20px;
}
.ma-mom-singleMenu {
  width: 100%;
}
.ma-mom-singleMenu img {
  width: 40%;
}
.mom-countdown {
  width: 100%;
  height: 60px;
  background: #000;
}
.mom-countdown > span {
  display: inline-block;
  text-align: center;
  margin-left: 10%;
  height: 60px;
  line-height: 60px;
  font-family: PingFangSC-Regular;
  font-size: 17px;
  color: #FFFFFF;
  letter-spacing: 0;
}
.activity-clocker {
  display: inline-block !important;
  width: 30px !important;
  height: 30px !important;
  line-height: 30px;
  text-align: center;
  background: #808080;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #FFF;
  letter-spacing: -0.69px;
}
.clocker-list {
  display: inline-block;
  width: 80%;
  margin: 0 auto;
}
.clocker-list > li {
  display: inline-block;
  width: 20%;
  margin: 2px;
  padding-top: 2px;
  text-align: center;
  font-family: PingFangSC-Regular;
  font-size: 10px;
  color: #FFFFFF;
  letter-spacing: -0.27px;
}
.mom-fortune-have {
  width: 98%;
  margin: 5px auto;
  text-align: center;
}
.mom-fortune-have > li {
  display: inline-block;
  width: 18%;
  margin: 0 1%;
}
.mom-fortune-have > li > div {
  position: relative;
  width: 100%;
  height: 70px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.mom-fortune-have > li > div img {
  width: 100%;
  height: 70px;
}
.mom-fortune-have > li > div span {
  z-index: 10;
  position: absolute;
  top: 2px;
  right: 2px;
}
.gameJoinInfo {
  display: -webkit-flex;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  background: #C3C1C1;
  height: 70px;
}
.gameJoinInfo img {
  width: 50px;
  height: 50px;
  margin-top: 8px;
}
.helperMarquee {
  position: relative;
  width: 90%;
  margin: 10px auto;
  background: #FFFFFF;
  border: 2px solid #B4B2B2;
  border-radius: 8px;
}
.helperMarquee-title {
  width: 100%;
  height: 36px;
  line-height: 36px;
  background: #000;
  font-family: PingFangSC-Medium;
  font-size: 13px;
  color: #EBCE44;
  letter-spacing: -0.27px;
}
.helperMarquee-body {
  min-height: 30px;
  max-height: 140px;
  overflow-x: hidden;
  line-height: 45px;
}
.helperMarquee-title {
  display: -webkit-flex;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.helperMarquee-title div {
  margin-left: -12%;
  text-align: left;
}
.helperMarquee-body > li {
  display: -webkit-flex;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.helperMarquee-body > li div {
  width: 35%;
  margin-left: 5px;
  white-space:nowrap;
  overflow: hidden;
}
.helperMarquee-body > li div:first-child {
  width: 30%;
}
.helperMarquee-body > li img {
  width: 25px;
  height: 25px;
  position: relative;
  top: 6px;
  margin-left: 5px;
}
.mom-reward-list {
  width: 100%;
  text-align: center;
}

/*.mom-reward-list li {
  width: 100%;
}*/
.mom-reward-list li {
  width: 40%;
  margin: 10px;
  color: #FFF;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.singleCardGift > div {
  z-index: 2;
}
.singleCardGift > div span {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  width: 40%;
  overflow-x: hidden;
}
.singleCardGift > div:nth-child(2) {
  position: relative;
  z-index: 1;
  background-size: 100% 100%;
  padding-bottom: 100%;
  height: 0;
  background-position: center center;
}
.singleCardGift > div:nth-child(2) > div {
  z-index: 10;
  position: absolute;
  top: 25%;
  left: 25%;
  height: 50%;
  width: 50%;
  background: rgba(0, 0, 0, 0.5);
}
.singleCardGift > div:nth-child(3) {
  color: rgb(228, 220, 160);
  font-weight: thin;
}
.singleCardGift > div:nth-child(4) {
  display: inline-block;
  color: rgb(255, 226, 200);
  font-weight: bold;
  width: 100%;
  overflow-x: hidden;
}
.mom-reward-list li div {
  width: 100%;
  display: inline-block;
}
.mom-reward-list img {
  width: 100%;
  height: 100%;
}
.mom-activity-rule {
  width: 94%;
  margin: 10px auto;
  border: 2px #666 dotted;
  padding-left: 10px;
  padding-bottom: 10px;
}
.mom-activity-rule > dl {
  margin: 10px auto;
}
.mom-activity-rule > dl > dd, .mom-activity-rule > dl > dt {
  display: inline-block;
  font-size: 14px;
}
.mom-activity-rule > dl > dd {
  background: #000;
  color: #fff;
  padding-left: 5px;
  padding-right: 5px;
}
.mom-activity-rule > dl > dt {
  color: #000;
  margin-left: 5px;
  overflow-wrap: nowrap;
}
.mom-activity-rule > h3 {
  margin-top: 30px;
}
.mom-activity-rule > p {
  font-size: 14px;
  color: #000;
}
.mom-order-form, .mom-blessing-form {
  width: 100%;
  background: #BC0B0B;
  padding-bottom: 10px;
  margin: 0 auto;
}
.mom-order-footer {
  background: #FFF;
  margin-top: -10px;
}
.mom-order-footer > p {
  /* : */
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #000000;
  letter-spacing: -0.25px;
  width: 80%;
  margin: 5px auto;
  padding-top: 10px;
  text-align: center;
}
.mom-order-footer button {
  width: 80% !important;
  background: #000;
  margin-bottom: 10px;
  font-family: PingFangSC-Medium;
  font-size: 13px;
  color: #FFFFFF;
  letter-spacing: 2em;
  padding-left: 3em;
  height: 40px;
}
.mom-order-form .weui-label {
  font-size: 16px;
  position: relative;
  top: 4px;
  padding-right: 5px;
}
.mom-order-form .weui-input {
  background-color: #FFF !important;
  height: 30px;
}
.mom-order-form .weui-input:hover {
  outline: none;
}
.mom-blessing-form {
  overflow-y: hidden;
}
.mom-blessing-form img {
  width: 60%;
  margin: 10px auto;
  margin-bottom: -15px;
}



