
.act_page {
  background: #fff;
  width: 100%;
}
.actImage{
  width: 100%;
  margin:-4px auto;
}
.actImage:first-child {
  margin-bottom: -11px;
}
.act_title{
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #4A4A4A;
  letter-spacing: -0.46px;
  text-align: left;
  line-height: 1.1em;
  margin-top: 10px;
  margin-bottom: 10px;
}
.act_tips {
  display: block;
  width: 100%;
  text-align: left;
  font-size: 12px;
  color: #9B9B9B;
  letter-spacing: -0.34px;
}
.userInfos {
  width: 90%;
  margin:5px auto;
  text-align: center;
  padding-top: 24px;
  padding-bottom: 24px;
  margin-bottom: 6px;
}
input[type='text'] {
  /* background: #eee; */
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  color: #999999;
  border-radius: 2.5px;
  margin: 10px auto;
  margin-left: -8px;
  margin-bottom: 0;
  border: 1px #eee solid;
  text-align: left;
}
input[type='text']:hover {
  outline: 1px #999 solid;
}
.weui-check_label{
  display: inline-block!important;
  width: 49%!important;
  border: 1px red solid!important;
}
.radioGroup,.selectGroup {
  width: 90%;
  margin: 5px auto;
}
.demo1-item {
  width: 50%;
  margin: 2px auto;
  /* 3: */
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #4A4A4A;
  letter-spacing: -0.4px;
}
.demo1-item>i[class~='icon-circle'] {
  display: inline-block;
  padding-right: 10px;
}
.demo1-item>i[class~='icon-checkbox'] {
  display: none;
}
.demo1-item-selected>i[class~='icon-circle'] {
  display: none;
}
.demo1-item-selected>i[class~='icon-checkbox'] {
  display: inline-block;
  padding-right: 10px;
  color: #ED8C7F;
}



